.minRate * 100).toFixed(2)\r\n                };\r\n\r\n                this.tableData.push(gold);\r\n            }\r\n        }\r\n    },\r\n    mounted () {\r\n\r\n        axios.get('https://129.226.121.194:8443/gold-price').then((response) => {\r\n            console.log(response);\r\n            if(200 != response.status) {\r\n                console.error('gold-price error response');\r\n                return;\r\n            }\r\n            let goldData = response.data.data.result.datals;\r\n            this.formaterData(goldData);\r\n        });\r\n    }\r\n}\r\n</script>\r\n<style>\r\n    .el-table .up-row {\r\n        background: red;\r\n        color: white;\r\n    }\r\n\r\n    .el-table .down-row {\r\n        background: green;\r\n        color: white;\r\n    }\r\n</style>","\n.el-table .up-row {\n    background: red;\n    color: white;\n}\n.el-table .down-row {\n    background: green;\n    color: white;\n}\n"]}]);
